@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700);
@import url(https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i);
body {
  padding: 50px 0px;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}
@media only screen and (min-width: 1200px) {
  body {
    padding: 50px 100px;
  }
}
#medium-portfolio-app {
  --color-bg: #e6e6e655;
}

:root {
  --color-action: #e31b6d;
  --color-bg: #d2dbdd;
  --color-chip-bg: #f2f2f2;
  --color-chip-bg-hover: #e6e6e6;
}

#medium-portfolio-app {
  font-family: "Roboto Slab", serif;
  background-color: #d2dbdd;
  background-color: var(--color-bg);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.chip {
  display: inline-block;
  color: #292929;
  transition: background 300ms ease 0s;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  padding: 1px 4px;
  margin: 0 2px;
  height: 12px;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  border-radius: 25px;
  background-color: var(--color-chip-bg);
}

.chip:hover {
  background-color: var(--color-chip-bg-hover);
}

:host {
  max-width: 100%;
}

.card {
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  width: 320px;
  border-radius: 12px;
  transform: scale(0.95, 0.95);
  overflow: hidden;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1, 1);
  z-index: 2;
}

.card-time {
  font-size: 12px;
  color: #ad7d52;
  vertical-align: middle;
  margin-left: 5px;
}

.card-clock-info {
  float: right;
}

.card-img {
  visibility: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card-info-hover {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
}

.card-img--hover {
  transition: 0.2s all ease-out;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  position: absolute;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  top: 0;
}

.card-info {
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 16px 24px;
  height: 155px;
}

.card-category {
  font-family: "Raleway", sans-serif;
  font-size: 10px;
  letter-spacing: 1.2px;
  font-weight: 500;
  color: #868686;
}

.card-title a {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Roboto Slab", serif;
  text-decoration: none;
  color: black;
}

.card-footer {
  margin-top: auto;
  font-size: 13px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  width: 100%;
}

.card-author {
  font-weight: 600;
  text-decoration: none;
  color: var(--color-action);
  margin-left: 4px;
}

.card-date {
  float: right;
}

.card:hover .card-img--hover {
  height: 100%;
  opacity: 0.3;
}

.card:hover .card-info {
  background-color: transparent;
  position: relative;
}

.card:hover .card-info-hover {
  opacity: 1;
}

.card-category {
  margin-top: auto;
}

@media only screen and (max-width: 580px) {
  .card {
    width: 100%;
    transform: scale(1, 1);
    border-radius: 0;
    margin-top: 10px;
  }
  .card-img,
  .card-info,
  .card-img--hover {
    border-radius: 0 !important;
  }
}

.avatar {
  width: 50px;
  border-radius: 50%;
  box-shadow: 0px 13px 10px -7px rgb(0 0 0 / 10%);
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}
h1 {
  font-weight: 300;
}

.avatar {
  margin-left: 15px;
}

.change-user {
  margin-bottom: 10px;
}

